body{
  padding: 0;
  margin: 0;
}
section{
width: 400px;
  /* max-width: 100%; */
  background-color: rgb(0, 0, 189);
  text-align: center;
  padding: 10px 10px;
  border-radius: 20px;
  position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(85, 199, 237);
  border-radius: 20px;
}

.item-input{
  /* background-color: rgb(85, 199, 237); */
  background-color: transparent;
  font-size: 18px;
  border: none;
  padding: 10px;
  width: 90%;
  border-radius: 50px;
  text-decoration: none;
  outline: none;
  caret-color: white;
}

/* input[type="text"] { 
  caret-color: rgb(0, 0, 189); 
} */

::placeholder {
  color: rgb(0, 0, 189);
  opacity: 1; 
} 

 ::-ms-input-placeholder {
  color: rgb(0, 0, 189);
}

.add-button{
   background-color: transparent;
   border: none;
   color: rgb(0, 0, 189);
   padding: 0 15px;
   cursor: pointer;
}

.item{
  display: flex;
  justify-content: space-between;
  align-items: center;
  
  border-bottom: 1px solid white;
}

.item-name{
  font-size: 18px;  
  width: 80%;
}

.item-list{ 
  color: white;
  width: 90%;
  text-align: center;
}

/* .items-div{
  display: flex;
  justify-content: space-between;
} */

.delete-button{
  text-decoration: none;
  background-color: transparent;
  outline: none;
  border: 1px solid rgb(85, 199, 237);
  border-radius: 20px;
  color: white;
  padding:  5px 15px;
}

@media only screen and (max-width: 600px){
  section{
    width: 300px;
    }
}